*{
    padding: 0;
    margin: 0;
    box-sizing: border-box;
  
    
}
.box{
  display: flex;
  justify-content: center;
 margin-top: 100px;
  
}

.content{
  font-family: "Source Sans Pro",monospace;
  height: 400px;
  width: 400px;
  border-radius: 10px;
  background-color: #e5e5e5;
  position: relative;
  font-weight: 400;
  font-style: italic;
  box-shadow: 0 0 20px black;
  opacity: 0;
  
  

}
.content-visible{
  font-family: "Source Sans Pro",monospace;
  height: 400px;
  width: 400px;
  border-radius: 10px;
  background-color: #e5e5e5;
  position: relative;
  font-weight: 400;
  font-style: italic;
  box-shadow: 0 0 20px black;
  opacity: 1;
  
  transition: 10s;

}
.abhinay{
  height: 400px;
  width: 400px;
 border-radius: 10px;
 box-shadow: 0 0 20px black;
 transform: translateX(100%);
 opacity: 0;

}
.abhinay-animate{
  height: 400px;
  width: 400px;
 border-radius: 10px;
 box-shadow: 0 0 20px black;
  transform: translateX(0);
  transition: 2s;
  opacity: 1;
}
.name{
  margin-top: 130px;
  margin-left: 10px;
  width: max-content;
  position: relative;
  
  
}
.name::before,
.name::after{
  content: "";
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
}
.name::before{
  background-color: #e5e5e5;
  animation: typewriter 3s steps(20) forwards;

}
@keyframes typewriter{
  to{
    left: 100%;
  }
}
h3{
  margin-top: 30px;
  margin-right:20px ;
  margin-left:10px ;
}
.abhinay-animate:hover{
  box-shadow: 0 0 20px #003049;
}
.content-visible:hover{
  box-shadow: 0 0 20px #003049;

}
