*{
    padding: 0;
    margin: 0;
    box-sizing: border-box;

}
.outer{
    display: flex;
   justify-content: center;
    margin: 50px;
}

.container{
    height: 300px;
    width: 700px;
    background-color: 	#28282B;
    border-radius: 50px;
    position: relative;
    
}
.tech{
    position: absolute;
    left: 250px;
    color: white;
}
.img3{
    height: 100px;
    border-radius: 15px;
    width: 100px;
    transform: translateX(-100%);
    opacity: 0;
    filter: blur(1px);
}
.img3-visible{
    height: 100px;
    border-radius: 15px;
    width: 100px;
    transform: translateX(0);
    opacity: 1;
    filter: blur(0);
    transition: 2s;


}
.img-container{
    display: grid;
    grid-template-columns: repeat(4,1fr);
    margin: 100px;
    
}
.img3-visible:hover{
    transform: translateY(-10px);
    transition: ease 1s;
}
.skill:nth-child(2){
    transition-delay: 200ms;
}
.skill:nth-child(3){
    transition-delay: 400ms;
}
.skill:nth-child(4){
    transition-delay: 600ms;
}


