*{
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}
.background{
    background-color: black;
    margin-top: 30px;
   height: 400px;
}
.card-container{
    display: flex;
    justify-content: space-evenly;
   position: relative;
    
    
}
.card1{
    height: 300px;
    width: 300px;
    background-color: #ccd5ae;
    border-radius: 10px;
    display: flex;
    flex-direction: column;
   align-items: center;
   position: relative;
   top: 50px;
   opacity: 0;
   filter: blur(1px);
   transform: translateX(-100%);
   
}
.card1-visible{
    height: 300px;
    width: 300px;
    background-color: #ccd5ae;
    border-radius: 10px;
    display: flex;
    flex-direction: column;
   align-items: center;
   position: relative;
   top: 50px;
   transform: translateX(0);
   filter: blur(0);
   opacity: 1;
   transition: 3s;

}
.text{
    margin-top: 150px;
    margin-bottom: 50px;
    margin-left: 500px;
   


}
.card1img{
    height: 200px;
    width: 300px;
    border-radius: 10px;
}
.card1-visible:hover{
    transform: translateY(-5px);
    transition: ease 0.5s;
    box-shadow: 0 0 20px white;
}
.logo:nth-child(2){
    transition-delay: 400ms;
}
.logo:nth-child(3){
    transition-delay: 600ms;
}