*{
    padding: 0;
    margin: 0;
    box-sizing: border-box;
    
    
}
nav{
    /* background-color: #89CFF0; */
    margin-top: 10px;
    position: relative;
  
}
ul{
    height: 40px;
    display: flex;
    list-style: none;
    justify-content: space-evenly;
    align-items: center;
   
}

li:hover{
    transform: translateY(-5px);
    cursor: pointer;
    
   
    /* color: white; */
}
li{
position: relative;

}
li::after{
    content: "";
    height: 2px;
    width: 100%;
   
    
    background: blueviolet;
    position: absolute;
    left: 0;
    bottom: -8px;
    opacity: 0;
    transition: all 0.4s;
    pointer-events: none;


}
li:hover::after{
    opacity: 1;
    pointer-events: none;
}
